import Immutable from 'immutable';
import {types as authTypes} from './auth.redux';
import {types as userTypes} from './users.redux';
import {types as groupsTypes, getGroupIds} from './groups.redux';
import {getLocalStorage} from "../utils/utils";
import isEmpty from 'lodash/isEmpty';

const defaultState = Immutable.fromJS({
  isRememberAccount: false,
  deviceStatus: ['transmission', 'online', 'offline'], // , , 
  deviceType: ['encode', 'wowza'], // encode, wowza
  selectedItem: '', // , <select>, group_id
  selectedMenuItem: '', // <Menu>, device/groups/server.js
});

// action types
export const types = {
  OPEN_REMEMBER_ACCOUNT: 'UI/OPEN_REMEMBER_ACCOUNT', //   
  CLOSE_REMEMBER_ACCOUNT: 'UI/CLOSE_REMEMBER_ACCOUNT', //   
  SET_DEVICE_STATUS: 'UI/SET_DEVICE_STATUS', // 
  CLEAR_DEVICE_STATUS: 'UI/CLEAR_DEVICE_STATUS', // 
  SET_DEVICE_TYPE: 'UI/SET_DEVICE_TYPE', // 
  CLEAR_DEVICE_TYPE: 'UI/CLEAR_DEVICE_TYPE', // 
  SET_SELECTED_ITEM: 'UI/SET_SELECTED_ITEM', // <select>
  SET_SELECTED_MENU_ITEM: 'UI/SET_SELECTED_MENU_ITEM', // <Menu>
};

// action creators
export const actions = {
  openRememberAccount: () => ({
    type: types.OPEN_REMEMBER_ACCOUNT
  }),
  closeRememberAccount: () => ({
    type: types.CLOSE_REMEMBER_ACCOUNT
  }),
  setDeviceStatus: (dStatus) => ({
    type: types.SET_DEVICE_STATUS,
    dStatus
  }),
  clearDeviceStatus: () => ({
    type: types.CLEAR_DEVICE_STATUS
  }),
  setDeviceType: (dType) => ({
    type: types.SET_DEVICE_TYPE,
    dType
  }),
  clearDeviceType: () => ({
    type: types.CLEAR_DEVICE_TYPE
  }),
  setSelectedItem: (group_id) => ({
    type: types.SET_SELECTED_ITEM,
    group_id
  }),
  setSelectedMenuItem: (current) => ({
    type: types.SET_SELECTED_MENU_ITEM,
    current
  })
};

// reducers
export default (state = defaultState, action) => {
  function getLoginSelectedStatus(state, groupsIds) { // 1: (/)
    return state.merge({
      selectedItem: groupsIds.length ? groupsIds[0] : '',
      selectedMenuItem: ''
    });
  }

  function getRefreshSelectedStatus(state, groupIds) { // 2: 
    let selectedItem = getLocalStorage('selectedItem');
    let selectedMenuItem = getLocalStorage('selectedMenuItem');
    if (isEmpty(selectedItem)) {
      return getLoginSelectedStatus(state, groupIds);
    }
    return state.merge({selectedItem, selectedMenuItem});
  }

  function getOperateSelectedStatus(state, group_id) { // : (/)
    return state.merge({
      selectedItem: group_id, // : id/: ''
      selectedMenuItem: 'device'
    });
  }

  switch (action.type) {
    case types.OPEN_REMEMBER_ACCOUNT:
      return state.set('isRememberAccount', true);
    case types.CLOSE_REMEMBER_ACCOUNT:
      return state.set('isRememberAccount', false);
    case types.SET_DEVICE_STATUS:
      return state.merge({deviceStatus: Immutable.fromJS(action.dStatus)}); //  []
    case types.CLEAR_DEVICE_STATUS:
      return state.merge({deviceStatus: Immutable.fromJS([])}); //  []
    case types.SET_DEVICE_TYPE:
      return state.merge({deviceType: Immutable.fromJS(action.dType)}); //  []
    case types.CLEAR_DEVICE_TYPE:
      return state.merge({deviceType: Immutable.fromJS([])}); //  []
    case types.SET_SELECTED_ITEM:
      return state.set('selectedItem', action.group_id); // 
    case types.SET_SELECTED_MENU_ITEM:
      return state.set('selectedMenuItem', action.current); // 
    case authTypes.LOGIN: // 
      return getLoginSelectedStatus(state, action.groupsIds); // 
    case userTypes.FETCH_USER_INFO: // 
      return getRefreshSelectedStatus(state, action.groupsIds); // 
    case groupsTypes.CREATE_GROUP: // 
      return getOperateSelectedStatus(state, action.group_id); // 
    case groupsTypes.DELETE_GROUP: // 
      return getOperateSelectedStatus(state, ''); // 
    default:
      return state;
  }
};

// selector
export const isRememberAccount = (state) => state.getIn(['ui', 'isRememberAccount']);
export const getDeviceStatus = (state) => state.getIn(['ui', 'deviceStatus']);
export const getDeviceType = (state) => state.getIn(['ui', 'deviceType']);
export const getSelectedItem = (state) => state.getIn(['ui', 'selectedItem']);
export const getSelectedMenuItem = (state) => state.getIn(['ui', 'selectedMenuItem']);

