import Immutable from 'immutable';

const defaultState = Immutable.fromJS({});

export const types = {
  SET_RESPONSE_STATE: 'RESPONSE/SET_RESPONSE_STATE',
  CLEAR_RESPONSE_STATE: 'RESPONSE/CLEAR_RESPONSE_STATE'
};

export const actions = {
  setResponseState: (data) => ({
    type: types.SET_RESPONSE_STATE,
    payload: data
  }),
  clearResponseState: () => ({
    type: types.CLEAR_RESPONSE_STATE
  }),
};

export default (state = defaultState, action) => {
  switch (action.type) {
    case types.SET_RESPONSE_STATE:
      const {data, ...params} = action.payload;
      return state.merge(Immutable.fromJS(params));
    case types.CLEAR_RESPONSE_STATE:
      return Immutable.fromJS({});
    default:
      return state;
  }
};

// selector
export const getResponseInfo = (state) => state.get('response');

