import {fromJS} from 'immutable';
import axios from 'axios';
import {actions as appActions} from './app.redux';
import {actions as responseActions} from './response.redux';
import {types as usersTypes} from './users.redux';
import url from '../utils/url';
import isEmpty from 'lodash/isEmpty';

const defaultState = fromJS({
  username: null,
});

// action types
export const types = {
  LOGIN: 'AUTH/LOGIN',
  LOGOUT: 'AUTH/LOGOUT',
  MODIFY_PASSWORD: 'AUTH/MODIFY_PASSWORD',
};

// action creators
export const actions = {
  login: ({user, password, verifyCode}) => {
    return (dispatch) => {
      dispatch(appActions.startRequest());
      dispatch(responseActions.clearResponseState());
      axios.post(url.login(), {user, password, verifyCode})
        .then((res) => {
          dispatch(appActions.finishRequest());
          dispatch(responseActions.setResponseState(res.data));
          console.log('[auth.redux login]', res);
          if (res.status === 200 && res.data.status) {
            const {username, groups, groupsIds} = convertLoginInfoToPlain(res.data.data);
            dispatch(loginSuccess(username, groups, groupsIds));
            /**
             * auth.redux.js
             * groups.redux.js
             * */
          } else {
            // 
            const {msg, code, info} = res.data;
            dispatch(appActions.setError(msg));
          }
        })
        .catch((err) => {
          dispatch(appActions.setError(err));
        });
    }
  },
  logout: () => {
    return (dispatch) => {
      dispatch(appActions.startRequest());
      dispatch(responseActions.clearResponseState());
      axios.get(url.logout())
        .then((res) => {
          dispatch(appActions.finishRequest());
          dispatch(responseActions.setResponseState(res.data));
          console.log('[auth.redux logout]', res);
          if (res.status === 200 && res.data.status) {
            dispatch(logoutSuccess());
            /**
             * auth.redux.js 
             * groups.redux.js 
             * */
          }
        })
        .catch((err) => {
          dispatch(appActions.setError(err));
        })
    }
  },
  modifyPassword: ({user, old_password, new_password, verifyCode}) => {
    return (dispatch) => {
      dispatch(appActions.startRequest());
      dispatch(responseActions.clearResponseState());
      axios.post(url.modifyPassword(), {user, old_password, new_password, verifyCode})
        .then((res) => {
          dispatch(appActions.finishRequest());
          dispatch(responseActions.setResponseState(res.data));
          console.log('[auth.redux modifyPassword]', res);
          if (res.status === 200 && res.data.status) {
            // dispatch(modifyPasswordSuccess(res.data.data)); // TODO: ???
          } else {
            // dispatch(); // TODO: ???
          }
        })
        .catch((err) => {
          dispatch(appActions.setError(err));
        });
    }
  }
};

// action creators
const loginSuccess = (username, groups, groupsIds) => ({
  type: types.LOGIN,
  username,
  groups,
  groupsIds
});

const logoutSuccess = () => ({
  type: types.LOGOUT
});

// TODO: , ???, 
// const modifyPasswordSuccess = () => ({
//   type: types.MODIFY_PASSWORD
// });

// , 
const convertLoginInfoToPlain = (data) => {
  const name = data.userInfo.name;
  const groupList = data.groupList;
  let groupsById = {};
  let groupsIds = [];
  groupList.forEach((item) => {
    groupsById[item.group_id] = {...item};
    groupsIds.push(item.group_id);
  });
  return {
    username: name,
    groups: groupsById,
    groupsIds
  }
};


// reducers
export default (state = defaultState, action) => {
  switch (action.type) {
    case usersTypes.FETCH_USER_INFO: // 
    case types.LOGIN: // 
      return state.merge({username: action.username});
    case types.LOGOUT: // 
      return state.merge(defaultState);
    default:
      return state;
  }
};

// selectors
export const getLoggedUser = (state) => state.get('auth');

