import Immutable from 'immutable';
import {combineReducers} from 'redux-immutable';
import axios from 'axios';
import {actions as appActions} from './app.redux';
import {actions as responseActions} from './response.redux';
import {types as authTypes} from "./auth.redux";
import url from '../utils/url';

// action types
export const types = {
  FETCH_WOWZA_INFO: 'WOWZA/FETCH_WOWZA_INFO', // wowza
};

// action creators
export const actions = {
  fetchWowzaInfo: ({group, group_id, recvStreamServices_id}) => {
    return (dispatch) => {
      dispatch(appActions.startRequest());
      // dispatch(responseActions.clearResponseState());
      axios.post(url.fetchWowzaInfo(), {group, group_id, recvStreamServices_id})
        .then((res) => {
          dispatch(appActions.finishRequest());
          // dispatch(responseActions.setResponseState(res.data));
          console.log('[wstream.redux fetchWowzaInfo]', res);
          if (res.status === 200 && res.data.status) {
            const {wowzasInfo, wowzaIds} = convertSingleWowzaToPlain(res.data.data);
            dispatch(fetchWowzaInfoSuccess(group_id, wowzasInfo, wowzaIds));
          } else {
            dispatch(appActions.setError('wowza'));
          }
        })
        .catch((err) => {
          dispatch(appActions.setError(err));
        });
    }
  },
};

// action creators
const fetchWowzaInfoSuccess = (group_id, wowzasInfo, wowzaIds) => ({
  type: types.FETCH_WOWZA_INFO,
  group_id,
  wowzasInfo,
  wowzaIds
});

const convertSingleWowzaToPlain = (data) => {
  const {recvStreamServicesStatusInfo} = data;
  let wowzasById = {};
  let wowzaIds = [];
  recvStreamServicesStatusInfo.forEach((item) => {
    wowzasById[item.id] = {...item};
    wowzaIds.push(item.id);
  });
  return {
    wowzasInfo: wowzasById,
    wowzaIds
  }
};

const byId = (state = Immutable.fromJS({}), action) => {
  switch (action.type) {
    case types.FETCH_WOWZA_INFO:
      return state.merge(Immutable.fromJS(action.wowzasInfo)); //  {}
    case authTypes.LOGOUT:
      return Immutable.fromJS({}); // 
    default:
      return state;
  }
};

const byGroup = (state = Immutable.fromJS({}), action) => {
  switch (action.type) {
    case types.FETCH_WOWZA_INFO:
      return state.merge({[action.group_id]: Immutable.fromJS(action.wowzaIds)}); //  []
    case authTypes.LOGOUT:
      return Immutable.fromJS({}); // 
    default:
      return state;
  }
};

const reducer = combineReducers({
  byId,
  byGroup
});

export default reducer;

// selector
// wowzas
export const getWowzasStreamInfo = (state) => state.getIn(['wstream', 'byId']);
// wowzaid
export const getWowzaStreamInfoByGroup = (state, groupId) => state.getIn(['wstream', 'byGroup', groupId]);
// wowza
export const getWowzaInfoById = (state, id) => state.getIn(['wstream', 'byId', id]);


