import {fromJS} from 'immutable';
import axios from 'axios';
import {actions as appActions} from './app.redux';
import {actions as responseActions} from './response.redux';
import {types as authTypes} from './auth.redux';
import url from '../utils/url';

const defaultState = fromJS({
  verifyCodeImg: null
});

// action types
export const types = {
  FETCH_VERIFY_CODE_IMG: 'AUTH/FETCH_VERIFY_CODE_IMG',
};

// action creators
export const actions = {
  fetchVerifyCode: () => {
    return (dispatch) => {
      dispatch(appActions.startRequest());
      dispatch(responseActions.clearResponseState());
      axios.get(url.getVerifyCode(), {responseType: 'blob', emulateJSON: true})
        .then((res) => {
          dispatch(appActions.finishRequest());
          dispatch(responseActions.setResponseState(res.data));
          console.log('[vcode.redux fetchVerifyCode]', res);
          if (res.status === 200) {
            convertBlobToBase64(res)
              .then((base64) => {
                dispatch(fetchVerifyCodeSuccess(base64));
              });
          } else {
            dispatch(appActions.setError(''));
          }
        })
        .catch((err) => {
          dispatch(appActions.setError(err));
        });
    }
  }
};

// creators
const fetchVerifyCodeSuccess = (url) => ({
  type: types.FETCH_VERIFY_CODE_IMG,
  url
});

const convertBlobToBase64 = async (res) => {
  const blob = res.data;
  const base64 = await blobToBase64(blob);
  return base64;
};

const blobToBase64 = (blob) => {
  return new Promise((resolve, reject) => {
    const fileReader = new FileReader();
    fileReader.onload = (e) => {
      resolve(e.target.result);
    };
    // readAsDataURL
    fileReader.readAsDataURL(blob);
    fileReader.onerror = () => {
      reject(new Error(''));
    };
  });
};

// reducers
export default (state = defaultState, action) => {
  switch (action.type) {
    case types.FETCH_VERIFY_CODE_IMG:
      return state.merge({verifyCodeImg: action.url});
    case authTypes.LOGOUT:
      return state.merge(defaultState); // 
    default:
      return state;
  }
}


// selectors
export const getVerifyCodeImg = (state) => state.getIn(['vcode', 'verifyCodeImg']);

