import React from 'react';
import PropTypes from 'prop-types';
import videojs from 'video.js';
import '../../../node_modules/video.js/dist/video-js.css'; // , widthheight; , 

class SPlayer extends React.Component {
  constructor(props) {
    super(props);
    this.player = React.createRef();
  }

  static propTypes = {
    url: PropTypes.string.isRequired,
    eventOn: PropTypes.object.isRequired,
    width: PropTypes.string.isRequired,
    height: PropTypes.string.isRequired,
    posters: PropTypes.string.isRequired
  };

  render() {
    return (
      <div>
        <video
          className='video-js'
          data-setup='{}'
          poster={this.props.posters}
          ref={this.player}
          autoPlay
          controls
        />
      </div>
    )
  }

  componentDidMount() {
    const {eventOn} = this.props;
    const videoNode = this.player.current;

    this.video = videojs(videoNode, {
      sources: [{
        src: this.props.url,
      }],
      width: this.props.width,
      height: this.props.height,
    });

    // 
    Object.keys(eventOn).forEach((item) => {
      this.video.on(item, () => {
        eventOn[item]();
      })
    });
  }

  componentWillUnmount() {
    if (this.video) {
      this.video.dispose();
    }
  }
}

export default SPlayer;

