import React from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import {Redirect} from 'react-router-dom';
import Modal from '../../component/modal/modal'; // 
import {Form, Input, Icon, Checkbox, Button} from 'antd';
import {LoginWrapper, LoginContainer, LoginMain, Logo, Title} from './style';
import {actions as appActions, getError, getRedirectTo} from '../../redux/app.redux';
import {actions as authActions} from '../../redux/auth.redux';
import {actions as vcodeActions, getVerifyCodeImg} from '../../redux/vcode.redux';
import {actions as uiActions, isRememberAccount} from '../../redux/ui.redux';
import {getResponseInfo} from '../../redux/response.redux';
import {removeLocalStorage, getLocalStorage} from '../../utils/utils';


class Login extends React.Component {
  state = {
    confirmDirty: false,
    height: window.innerHeight, // 
  };

  render() {
    // console.log('[login]', getLocalStorage('selectedMenuItem'));
    const {verifyCodeImg, isRememberAccount, redirectTo, error, responseInfo} = this.props;
    return (
      <LoginMain>
        {/*{error ? <Modal type='error' message={''} description={error}/> : null}*/}
        {/*, home.js*/}
        {
          (responseInfo.size && !responseInfo.get('status'))
            ? (
              <Modal
                type={'error'}
                message={responseInfo.getIn(['info', 'title'])}
                description={responseInfo.getIn(['info', 'info'])}/>
            )
            : null
        }
        {
          (redirectTo && redirectTo !== '/login' && redirectTo !== '/')
            ? (<Redirect to={redirectTo}/>)
            : null
        }
        <LoginWrapper style={{height: this.state.height}}>
          <LoginContainer style={{marginTop: (this.state.height * 3 / 14)}}>
            <Logo>
              <img src={require('../../images/logo.png')} alt=""/>
            </Logo>
            <Title>
              
            </Title>
            <LoginInputForm
              wrappedComponentRef={this.saveFormRef}
              onSubmit={this.handleSubmit}
              onChange={this.handleChange}
              verifyCodeImg={verifyCodeImg}
              isRememberAccount={isRememberAccount}
              onUpdateVCode={this.updateVCode}
            />
          </LoginContainer>
        </LoginWrapper>
      </LoginMain>
    )
  }

  saveFormRef = (formRef) => {
    this.formRef = formRef;
  };

  handleSubmit = (e) => {
    e.preventDefault();
    const {form} = this.formRef.props;
    form.validateFields((err, values) => {
      if (!err) {
        // console.log('', values);
        this.props.login(values);
        this.updateVCode();
      }
    });
  };

  // checkbox
  handleChange = (e) => {
    e.target.checked
      ? this.props.openRememberAccount()
      : this.props.closeRememberAccount()
  };

  // 
  updateVCode = () => {
    this.props.fetchVerifyCode();
  };

  componentDidMount() {
    // 
    this.props.fetchVerifyCode();
    // 
    const _this = this;
    window.onresize = function () {
      if (window.innerHeight > 600) {
        _this.setState({height: window.innerHeight});
      }
    };
    // localStorage
    removeLocalStorage('selectedItem');
    removeLocalStorage('selectedMenuItem');
  }
}


// 
const LoginInputForm = Form.create({name: 'login_input_form'})(
  class extends React.Component {
    render() {
      const {getFieldDecorator} = this.props.form;
      const {onSubmit, onChange, verifyCodeImg, isRememberAccount, onUpdateVCode} = this.props;
      return (
        <Form onSubmit={onSubmit}
              style={{width: '100%'}}
        >
          <Form.Item>
            {
              getFieldDecorator('user', {
                rules: [{required: true, message: 'Please input your username!'}]
              })(
                <Input
                  prefix={<Icon type='user' style={{color: 'rgba(0, 0, 0, .25)'}}/>}
                  placeholder='Username'
                  size='large'
                  allowClear
                />
              )
            }
          </Form.Item>
          <Form.Item>
            {
              getFieldDecorator('password', {
                rules: [{required: true, message: 'Please input your password!'}]
              })(
                <Input.Password
                  prefix={<Icon type='lock' style={{color: 'rgba(0,0,0,.25)'}}/>}
                  placeholder='Password'
                  type='password'
                  size='large'
                />
              )
            }
          </Form.Item>
          <Form.Item>
            {
              getFieldDecorator('verifyCode', {
                rules: [{required: true, message: 'Please input verifyCode!'}]
              })(
                <div>
                  <Input
                    prefix={<Icon type="barcode" style={{color: 'rgba(0,0,0,.25)'}}/>}
                    placeholder='verifyCode'
                    size='large'
                    style={{width: 'calc(100% - 80px)'}}
                  />
                  <div style={{
                    display: 'inline-block',
                    width: 80,
                    height: 40,
                    verticalAlign: 'top',
                  }}>
                    <div style={{
                      width: '100%',
                      height: '100%',
                      background: '#fff'
                    }}>
                      <img
                        // src={require('../../images/cnValidateImage.png')}
                        src={verifyCodeImg}
                        style={{width: '100%', height: '100%', verticalAlign: 'top', cursor: 'pointer'}}
                        alt=""
                        onClick={onUpdateVCode}
                      />
                    </div>
                  </div>
                </div>
              )
            }
          </Form.Item>
          <Form.Item>
            <Checkbox checked={isRememberAccount} onChange={onChange}
                      style={{color: '#fff', fontWeight: '700'}}
            >
              
            </Checkbox>
          </Form.Item>
          <Form.Item>
            <Button type='primary' htmlType='submit' className="login-form-button" size='large'
                    block
                    disabled={!verifyCodeImg}></Button>
          </Form.Item>
        </Form>
      )
    }
  }
);


const WrappedLoginForm = Form.create({name: 'login'})(Login);

const mapStateToProps = (state, props) => {
  return {
    verifyCodeImg: getVerifyCodeImg(state), //  
    isRememberAccount: isRememberAccount(state), // 
    redirectTo: getRedirectTo(state), // 
    error: getError(state), // 
    responseInfo: getResponseInfo(state), // 
  };
};

const mapDispatchToProps = (dispatch) => { // 
  return {
    ...bindActionCreators(authActions, dispatch),
    ...bindActionCreators(vcodeActions, dispatch),
    ...bindActionCreators(uiActions, dispatch),
    ...bindActionCreators(appActions, dispatch)
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(WrappedLoginForm);